.main{
    width: 100%;
 }   
.secTitle{
    position: relative;
    /*width: min-content;*/
    color: hsl(240, 4%, 36%);
}
.secContent {
    justify-content: center;
    gap: 1-5rem;
}
.singleDestination{
    height: 100%;
    display: grid;
    row-gap: 10px;
    border-radius: 10px;
    align-items: center;
    background: rgb(225, 225, 235);
    box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
    overflow: hidden;

}
.singleDestination:hover{
    background: hsl(0, 0%, 100%);
    box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
    transition:  .3s ease;
    
}
.imfDiv{
    width: 100%;
    height: 180px;
    overflow: hidden;
}
.img1{
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: 3s ease;
}
.img1:hover{
    transform: scale(1.1);
}
.cardInfo{
    padding: 2rem;
}
.desTitle{
    color: hsl(201,33%,16%);
    font-size: 1.25rem;
}
.continent{
    gap: .5rem;
}
.icon{
    font-size: 18px;
    color: hsl(240, 4%, 36%);
}
.name{
    color: hsl(240, 4%, 36%);
    font-weight: 400;
    font-size: 14px;
}
.fees{
    width: 100%;
    justify-content: space-between;
    padding: 1rem;
    margin: 1rem 0;
    border-top: 1.5px solid rgb(190, 190, 190);
    border-bottom: 1.5px solid rgb(190, 190, 190);
    }  
    
.grade{
    color: hsl(240, 4%, 36%);
    max-width: 100px;
    line-height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
small{
    text-align: center;
    background: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    border-radius: 1rem;
    color:hsl(0, 0%, 100%);
    padding: 1px 10px;
    font-size: 10px;
}
.price{
    font-size: 2rem;
    color: hsl(201,33%,16%);
}
.desc{
    font-size: 13.5px;
    color: hsl(240, 4%, 36%);
    font-weight: 500;
}
.btn{
    margin-top: 1rem;
    color: hsl(0, 0%, 100%);
    font-weight: 600;
    justify-content: space-between;
    gap: .5rem;
    
}
.icon{
    align-self: center;
    font-size: 18px;
}


@media screen and (min-width: 660px){
    .content {
        margin-bottom: 2rem;
    }
    
}