.about-section {
    padding: 50px;
    text-align: center;
    background:  linear-gradient(to right, hsl(187,85%,43%),hsl(199, 100%, 33%));
    color: white;
    width: 100%;
    border-radius: 10px;
  }
  .card{
    overflow: hidden;
    background: rgb(225, 225, 235);
    box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
    
    
  }
 
img{
    object-fit: contain;
    overflow: hidden;
    transition: 3s ease;
}
  img:hover{
    transform: none;
}

.card-body:hover{
    background: hsl(0, 0%, 100%);
    box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
    transition:  .3s ease;
    
}
.card-text1{
    font-size: 13px;
    color: hsl(240, 4%, 36%);
}
.icon_1{
    font-size: 18px;
    color: hsl(240, 4%, 36%);
}
h3{
    color: hsl(240, 4%, 36%);
}
.card-title{
    color: hsl(201,33%,16%);
}
