.home{
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
}


.overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgb(47,106,127);
    top: 0;
    bottom: 0;
    z-index: 1;
    mix-blend-mode: hard-light;
    
    
}
.video1{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    object-fit: cover;
    
}
.homeContent{
    width: 100%;
    height: max-content;
    padding: 8rem 1.5rem 3rem;
    display: flex;
    row-gap: 3rem;
    flex-direction: column;
    align-items: stretch;
    margin: auto;
    color: hsl(0,0%,100%);
    z-index: 100;
}
.texDiv{
    padding: 2rem 0;
}
.smallText{
    font-size: 13px;
    text-transform: uppercase;
}
.homeTitle{
    position: relative;
    font-size: 1.8rem;
}
.cardDiv{
    align-content: stretch;
    position: relative;
    padding: 2rem 1rem;
    background: hsl(0,0%,100%);
    border-radius: 10px;
    gap: 1rem;
}
label{
    display: block;
    color: hsl(240,4%,36%);
    padding-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
}
.label_total{
justify-content: space-between;
align-items: center;
}
.total{
    color: hsl(240,4%,36%);
    font-size: 18px;
    padding-bottom: 10px;
}
.input{
    height: 40px;
    padding: 0 1rem;
    background: rgb(239,239,239);
    border-radius: 3rem;        
}
input{
    background: none;
    border: none;
    outline: none;
    width: 100%;
    font-size: 13px;
    font-weight: 500;
}
&::placeholder{
    font-size: 13px;
}
.icon{
    font-size: 18px;
    color: hsl(199,100%,33%) !important;
    margin-left: 10px;
}
.filter{
    position: absolute;
    background: linear-gradient(to right, hsl(187,85%,43%),hsl(199, 100%, 33%));
    width: max-content;
    bottom: -0.7rem;
    column-gap: .8rem;
    padding: 5px 1rem;
    border-radius: 1rem;
    left: 50%;
    transform: translate(-50%);
    justify-content: center;
    cursor: pointer;
 
}
.bi-funnel-fill,span{
    font-size: 13px;
}
.filter:hover{
    background: hsl(187,85%,43%);
}
@media screen and (min-width: 600px) {

    .home{
        font-size: 16px;
    }
    .homeTitle{
        position: relative;
        font-size: 2.3rem;
        width: max-content;
        
    }
    .homeTitle::after{
        position: absolute;
        content: '';
        background: hsl(187,85%,43%);
        height: 5px;
        width: 145px;
        right: 0px;
        bottom: 5px;
        z-index: -1;

    }
    .cardDiv{
        grid-template-columns: repeat(1,1fr);
       
    }
}
