@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif;
}

html{
  scroll-behavior: smooth;
}

body{
  background: rgb(240,240,246);
  
}


.flex{
  display: flex;
  align-items: center;
}
.grid{
  display: grid;
  align-items: center;
}
.btn{
  background: linear-gradient(to right, hsl(187,85%,43%),hsl(199, 100%, 33%));
  border-radius: 50px;
  outline: none;
  cursor: pointer;
  border-style: none;

}
.btn a{
  font-weight: 500;
}
input{
  font-size: 100%;
}
.btn:hover{
  background: hsl(187,85%,43%);
  color: #fff;
}
.nav-item{
  list-style: none;
  font-weight: 500;
  

}

.icon{
  font-size: 1rem;
  cursor: pointer;
}
video{
  width: 100%;
  /*height: auto;*/
}
.bi-globe-americas{
  color: hsl(190,100%,33%);
 
  
  
}
.navbar-brand{
  font-weight: 450;
}

