.footer{
    width: 100%;
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
}
.videoDiv{
    position: absolute;
    height: 100%;
    width: 100%;
}
video{
    height: 100%;
    object-fit: cover;
}
.videoDiv::after{
    content: '';
    position: absolute;
    background: rgba(10, 84, 107, 0.307);
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    mix-blend-mode: multiply;
}
.secContent{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 2rem initial;
    height: max-content;
    align-items: center;
    justify-content: center;
    margin: auto;
    row-gap: 2rem;
    z-index: 100;
}
.contactDiv{
    justify-content: space-between;
    flex-direction: column;
    gap: 1rem;
}
small{
    font-size: 13px;
    font-weight: 400;
    color: hsl(0, 0, 100%);
}
h2{
    color: hsl(0, 0, 100%) !important;
    font-size: 1.8rem;
}
.inputDiv{
    width: 100%;
    gap: 1rem;
    flex-direction: column;
   
}
a{
    text-decoration: none;
    color: hsl(201, 33%, 16%);
}
.footerH{
    color: white;
    margin: .5rem 1rem;
}
.btn-1{
    width: 100%;
    background: linear-gradient(to right, hsl(187,85%,43%),hsl(199, 100%, 33%));
    border-radius: 50px;
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
    justify-content: center;
    gap: .3rem;
    margin-top: 1rem;
    margin-bottom: .5rem;
    height: 2.5rem;
    
    
}
.btn-1:hover{
    background: hsl(187,85%,43%) ;
}
.bi{
    font-size: 18px;
    color: hsl(0, 0, 100%);
}

.footerCard{
    position: relative;
    padding: 1rem 1rem 4rem;
    gap: 2rem;
    border-radius: 2rem;
    background: rgb(240, 240, 246);
    width: 100%;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;
}
.footerIntro{
    flex-basis: 50%;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    row-gap: .5rem;
}
.logo{
    font-weight: 600;
}
.icon{
    font-size: 20px;
    margin-right: 10px;
    color: black;
}
.footerParagraph{
    font-size: 13px;
    color: hsl(240, 4%, 36%);
}
.footerSocials{
    column-gap: .5rem;
}
.footerLinks{
    flex-basis: 50%;
    width: 100%;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;

}
.groupTitile{
    display: block;
    font-weight: 600;
    margin-bottom: .5rem;
}
.footerlist{
    font-size: 15px;
    color: hsl(240, 4%, 36%);
    transition: .3s ease-in-out;
}
.icon{
    font-size: 15px;
    color: hsl(240, 4%, 36%);
}
.icon:hover{
    color: brown;
    transform: translatex(7px);
}
.footerlist:hover{
    color: hsl(187,85%,43%);
    transform: translateX(7px);
}
.footerDiv{
    flex-direction: column;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: hsl(187,85%,43%);
    padding: .5rem 2rem;
}
.footerText{
    font-size: 12px; 
    color:  white;
 }
 